.error-404 {
	padding-bottom: 80px;
	text-align: center;
	padding-top: 80px;

	@include devices(md) {
		padding-top: 100px;
		padding-bottom: 100px;
	}

	@include devices(lg) {
		padding-bottom: 120px;
		padding-top: 120px;
	}

	&__img{
		max-width: 880px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		svg{
			@keyframes bootoms {
				0% {
					-webkit-transform: translateY(0px);
					transform: translateY(0px);
				}
				70% {
					-webkit-transform: translateY(20px);
					transform: translateY(20px);
				}
				100% {
					-webkit-transform: translateY(0px);
					transform: translateY(0px);
				}
			} 
			@keyframes rights {
				0% {
					-webkit-transform: translateX(0px);
					transform: translateX(0px);
				}
				70% {
					-webkit-transform: translateX(-20px);
					transform: translateX(-20px);
				}
				100% {
					-webkit-transform: translateX(0px);
					transform: translateX(0px);
				}
			} 
			#path-2 {
				animation: rights 5s ease-in infinite;
				-webkit-animation: rights 5s ease-in infinite;
				-webkit-transform-origin: center; 
				-ms-transform-origin: center; 
				transform-origin: center; 
				transform-box: fill-box;
			}
			#path-1 {
				animation: bootoms 5s ease-in infinite;
				-webkit-animation: bootoms 5s ease-in infinite;
				-webkit-transform-origin: center; 
				-ms-transform-origin: center; 
				transform-origin: center; 
				transform-box: fill-box;
			}
			margin-bottom: 70px;
			@media #{$md,$sm,$xs} {
				margin-bottom: 40px;
			}
		}
	}

	&__title {
		position: relative;
		margin-bottom: 12px;
		font-size: 40px;
		font-style: normal;
		font-weight: 700;
		line-height: 65%;
	}

	&__text {
		color: $driveschol-heading-text;
		font-size: 16px;
		font-style: normal;
		font-weight: $fw-sbold;
		line-height: 200%;
		letter-spacing: 0.16px;
		margin-bottom: 20px;
	}

	&__search {
		display: inline-flex;
		align-items: center;
		position: relative;
		margin-bottom: 20px;
		width: 100%;
		max-width: 550px;

		input[type=text] {
			position: relative;
			border: none;
			outline: none;
			display: block;
			background-color: $driveschol-gray2;
			color: $driveschol-text;
			font-size: 16px;
			width: 100%;
			max-width: 550px;
			padding-left: 70px;
			padding-right: 20px;
			height: 60px;
			&::placeholder{
				color: $driveschol-gray;
				font-size: 16px;
				font-style: normal;
				font-weight: $fw-sbold;
				line-height:  162.5%;
			}
		}

		&__btn {
			@include reset-btn;
			position: absolute;
			margin-right: 8px;
			top: 50%;
			left: 35px;
			transform: translateY(-50%);
			font-size: 18px;
			color: $driveschol-gray;
			font-weight: $fw-bold;
			a{
				text-transform: none;
			}
		}
	}
	&__btns{
		a{
			padding: 14px 39.44px;
			font-size: 16px;
			font-style: normal;
			font-weight: 700;
			line-height: 28px;
		}
	}
}